import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Dataset} from './dataset';
import {of} from 'rxjs/observable/of';
import {Resource} from './resource';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {catchError, tap} from 'rxjs/operators';
import {log} from 'util';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class SearchService {

  private datasetsUrl = 'http://localhost:8080/rest/search/';  // URL to web api
  constructor(private http: HttpClient){}

  getDatasets(): Observable<Dataset[]> {
    return this.http.get<Dataset[]>(this.datasetsUrl + 'all')
      .pipe(
        catchError(this.handleError('getDatasets', []))
      );
  }

  getDataset(id: string): Observable<Dataset> {
    const url = `${this.datasetsUrl + 'id'}/${id}`;
    return this.http.get<Dataset>(url);
  }

  getResource(datasetID: string, resourceID: string): Observable<Resource> {
    const url = `${this.datasetsUrl + 'dataset'}/${datasetID}/resource/${resourceID}`;
    return this.http.get<Resource>(url).pipe(
      catchError(this.handleError<Resource>(`getResource id=${resourceID}`))
    );
  }
  /* GET datasets whose name contains search term */
  searchDatasets(term: string): Observable<Dataset[]> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      return of([]);
    }
    const url = `http://localhost:8080/rest/manual/search/${term}`;
    return this.http.get<Dataset[]>(url);
  }
  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}



